*{margin: 0;padding: 0;}
.container {
  max-width: 1200px;
  margin: 0 auto;
  background-color: #cdeedc;
  padding: 20px;

}
.goods-field{
  display: flex;
  justify-content: space-evenly;
}
.goods-block{
  border: 1px solid #000;
  border-radius: 5px;
  height: 200px;
  width: 200px;
  text-align: center;
}
p+p,img, button{
  margin-top: 20px;
}
.cart-field{
  background-color: #dcffec;
  margin-top: 20px;
}

th, td{
  padding-left: 20px;
  text-align: center;
}